.hidden {
  visibility: hidden;
  display: none;
}

.loader-symbol {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  background: #ff3d00;
  margin: 20px auto;
  animation: rotationBack 1s ease-in-out infinite reverse;
  display: block;
  position: relative;
}

.loader-symbol:before {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  background: #ff3d00;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(45deg);
  box-shadow: 0 0 5px rgba(0, 0, 0, .15);
}

.loader-symbol:after {
  content: "";
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 5px rgba(0, 0, 0, .15);
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

